a la comunidad humana en su conjunto, en tanto científicamente se reconoce que ésta desciende de África. No obstante y como categoría jurídica – política, la III Conferencia Mundial contra el Racismo, la Discriminación Racial, la Xenofobia y demás formas conexas de intolerancia, adoptó este término para referirse a la diáspora africana. Como categoría jurídica, el concepto de afrodescendiente da estatus de sujeto de derechos internacionales (Unidad para la atención y reparación de las víctimas, s.f., p.20).")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"450px"},attrs:{"src":require("@/assets/curso/tema4/img6.png"),"alt":"Imagen decorativa"}})])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Comunidad negra de San Basilio de Palenque"}},[_c('div',{staticClass:"row align-items-center justify-content-center"},[_c('div',{staticClass:"col-lg-7 mb-4 mb-lg-0"},[_c('p',[_vm._v("Se diferencia del resto de comunidades negras del país por contar con un dialecto propio, producto de la mezcla de lenguas africanas con el castellano.")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"450px"},attrs:{"src":require("@/assets/curso/tema4/img7.png"),"alt":"Imagen decorativa"}})])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Raizales del Archipiélago de San Andrés y Providencia"}},[_c('div',{staticClass:"row align-items-center justify-content-center"},[_c('div',{staticClass:"col-lg-7 mb-4 mb-lg-0"},[_c('p',[_vm._v("Se diferencian de las comunidades negras del continente porque cuentan con un dialecto propio y con prácticas culturales y religiosas más cercanas a la cultura antillana y a la influencia anglosajona en la región Caribe (Unidad para la atención y reparación de las víctimas, s.f., p.26).")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"450px"},attrs:{"src":require("@/assets/curs